import React, { Component } from "react";
import "./Register.less";
import { Form, message, Icon, Input, Button, Checkbox, Spin } from "antd";
import http from "../../../utils/api";

class Register extends Component {
  state = {
    disabled: false,
    counts: 60,
    showSpin: false
  };

  componentDidMount = () => {};
  onLogin = () => {
    this.props.history.push({
      pathname: "/login"
    });
  };

  countDown = () => {
    let counts = 60;
    this.setState({
      disabled: true
    });
    let countdown = setInterval(() => {
      if (counts > 0) {
        counts--;
        this.setState({
          counts
        });
      } else {
        this.setState({
          disabled: false
        });
        clearInterval(countdown);
      }
    }, 1000);
  };
  getVerCode = async () => {
    const { form } = this.props;
    let res;
    if (form.getFieldValue("phone")) {
      this.countDown();
      try {
        res = await http().getVerCode({
          telephone: form.getFieldValue("phone")
        });
      } catch (error) {
        message.error(error.message);
      }
    } else {
      message.info("");
    }
  };
  register = async () => {
    const { form } = this.props;
    let res;
    this.props.form.validateFieldsAndScroll(async (err, values) => {
      if (err) {
        return;
      }
      let registerData = {
        Handphone: form.getFieldValue("phone"), // 
        userid: form.getFieldValue("postNumber"), //
        // occuptionNumber: form.getFieldValue("postNumber"), //
        validcode: form.getFieldValue("valid"), // 
        // hospital: form.getFieldValue("hospital"), // 
        nickname: form.getFieldValue("doctor"),
        // office:form.office,
        validresid: 616852937051
        // unionid: this.unionid,
        // openid: this.openid
      };
      let doctorData = {
        phoneNumber: form.getFieldValue("phone"), // 
        occupationNumber: form.getFieldValue("postNumber"), //
        hospital: form.getFieldValue("hospital"), // 
        doctor: form.getFieldValue("doctor"),
        email: form.getFieldValue("email"),
        post: form.getFieldValue("post") //
      };
      this.setState({
        showSpin: true
      });

      try {
        res = await http().register(registerData);
        if (res.data.error == 0) {
          message.success("");
          this.props.history.push({
            pathname: "/login",
            state: { doctorData }
          });
        } else {
          message.error(res.data.message);
        }
      } catch (error) {
        message.error(error.message);
      }
      this.setState({
        showSpin: false
      });
    });
  };
  render() {
    const { disabled, counts, showSpin } = this.state;
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="page">
        <div className="register-contain">
          <Spin spinning={showSpin}>
            <Form onSubmit={this.handleSubmit} className="login-form-userName">
              <h1></h1>
              <Form.Item>
                {getFieldDecorator("doctor", {
                  rules: [{ required: true, message: "!" }]
                })(
                  <Input
                    prefix={
                      <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder=""
                  />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("hospital", {
                  rules: [{ required: true, message: "!" }]
                })(
                  <Input
                    prefix={
                      <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder=""
                  />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("post", {
                  rules: [{ required: true, message: "!" }]
                })(
                  <Input
                    prefix={
                      <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder=""
                  />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("postNumber", {
                  rules: [{ required: true, message: "!" }]
                })(
                  <Input
                    prefix={
                      <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder=""
                  />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("email", {
                  rules: [
                    {
                      type: "email",
                      required: true,
                      message: "!"
                    }
                  ]
                })(
                  <Input
                    prefix={
                      <Icon type="mail" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder=""
                  />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("phone", {
                  rules: [{ required: true, message: "!" }]
                })(
                  <Input
                    prefix={
                      <Icon type="phone" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder=""
                  />
                )}
              </Form.Item>

              <Form.Item className="login-form-valid">
                {getFieldDecorator("valid", {
                  rules: [{ required: true, message: "!" }]
                })(
                  <Input
                    className="login-form-valid-input"
                    prefix={
                      <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder=" "
                  />
                )}
              </Form.Item>
              {disabled ? (
                <Button style={{ marginLeft: "10px" }} type="primary" disabled>
                  {counts}
                </Button>
              ) : (
                <Button
                  style={{ marginLeft: "10px" }}
                  type="primary"
                  onClick={this.getVerCode}
                >
                  
                </Button>
              )}
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button"
                onClick={this.register}
                size="large"
              >
                
              </Button>
              <div className="login-form-register">
                <a onClick={this.onLogin}></a>
              </div>
            </Form>
          </Spin>
        </div>
      </div>
    );
  }
}

export default Form.create()(Register);
