import React, { Component } from "react";
import {
  List,
  Tag,
  Divider,
  Table,
  message,
  Form,
  Button,
  Checkbox,
  Spin,
  Modal,
  Input
} from "antd";
import "./ApproveList.less";
import http from "../../../utils/api";
const applyDataBaseId = "621790784304"; //-
const { TextArea } = Input;

class ApproveList extends React.Component {
  state = {
    data: [{}],
    record: {},
    page: "listPage",
    spin: false,
    visible: false
  };
  componentDidMount = async () => {
    await this.getData();
  };
  getData = async () => {
    // this.setState({
    //   spin: true
    // });
    let res;
    try {
      res = await http().getTable({
        resid: applyDataBaseId
      });
      if (res.data.error === 0) {
        let data = [];
        res.data.data.map((item, index) => {
          item.number = index + 1;
          data.push(item);
        });
        this.setState({
          data
        });
      }
    } catch (error) {}
    // this.setState({
    //   spin: false
    // });
  };
  onCheck = record => {
    this.setState({
      page: "checkPage",
      record
    });
  };
  onBack = () => {
    this.setState({
      page: "listPage"
    });
  };
  onPassOrReject = async type => {
    let res;
    let data;
    if (type === 1) {
      data = [
        {
          ...this.state.record,
          isPass: "Y"
        }
      ];
    } else {
      data = [
        {
          ...this.state.record,
          isPass: "N"
        }
      ];
    }
    try {
      res = await http().modifyRecords({
        resid: applyDataBaseId,
        data
      });
      if (res.data.Error === 0) {
        message.success("");
      }
      this.setState({
        page: "listPage"
      });
      await this.getData();
    } catch (error) {
      message.error(error.message);
    }
  };
  render() {
    const { data, record, page, spin, visible } = this.state;
    const options = [
      { label: "", value: "" },
      { label: "", value: "" },
      { label: "", value: "" },
      { label: "", value: "" }
    ];
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    };
    const checkPage = (
      <Form {...formItemLayout} className="approveList-form">
        {page === "checkPage" ? (
          <Button
            icon={"rollback"}
            type="primary"
            onClick={this.onBack}
            className="approveList-btn"
          >
            
          </Button>
        ) : null}
        <div className="approveList-btns">
          {record.isPass ? null : (
            <React.Fragment>
              {" "}
              <Button
                className="approveList-btn"
                type="primary"
                onClick={() => {
                  this.onPassOrReject(1);
                }}
              >
                
              </Button>
              {/* <Button
                className="approveList-btn"
                type="danger"
                onClick={() => {
                  this.onPassOrReject(2);
                }}
              >
                
              </Button> */}
            </React.Fragment>
          )}
        </div>
        <h1>CHASE-IBD</h1>
        <h3></h3>
        <Form.Item label={<span>&nbsp;</span>}>
          <span>{record.doctor}</span>
        </Form.Item>
        <Form.Item label={<span>&nbsp;</span>}>
          <span>{record.hospital}</span>
        </Form.Item>
        <Form.Item label={<span>&nbsp;</span>}>
          <span>{record.post}</span>
        </Form.Item>
        <Form.Item label={<span>&nbsp;</span>}>
          <span>{record.phoneNumber}</span>
        </Form.Item>
        <Form.Item label={<span>E-mail&nbsp;</span>}>
          <span>{record.email}</span>
        </Form.Item>
        <Form.Item label={<span>IBD&nbsp;</span>}>
          <span>{record.teamPeople}</span>&nbsp;&nbsp;
          <span>{record.teamPeople2}</span>&nbsp;&nbsp;
          <span>{record.teamPeople3}</span>&nbsp;&nbsp;
        </Form.Item>
        <Form.Item label={<span>&nbsp;</span>}>
          <Checkbox.Group
            options={options}
            defaultValue={record.studyType}
            onChange={this.onChangeCheckbox}
          />
        </Form.Item>
        <h3></h3>

        <Form.Item label={<span>CD&nbsp;</span>}>
          <span>{record.CDPatientNumber}</span>
        </Form.Item>
        <Form.Item label={<span>UC&nbsp;</span>}>
          <span>{record.UCPatientNumber}</span>
        </Form.Item>
        <Form.Item label={<span>&nbsp;</span>}>
          <img
            src={record.doctorPhoto}
            alt="avatar"
            style={{ width: "200px", height: "200px" }}
          />
        </Form.Item>
      </Form>
    );
    const columns = [
      {
        title: "",
        dataIndex: "number",
        key: "number",
        width: 80
      },
      {
        title: "",
        dataIndex: "doctor",
        key: "doctor",
        width: 150
      },
      {
        title: "",
        dataIndex: "applyDate",
        key: "applyDate",
        width: 150
      },
      {
        title: "",
        dataIndex: "hospital",
        key: "hospital",
        width: 150
      },
      {
        title: "",
        dataIndex: "studyType",
        key: "studyType",
        width: 150
      },
      {
        title: "",
        dataIndex: "approveStatus",
        key: "approveStatus",
        width: 150,
        render: data => {
          return (
            <Tag
              color={
                data === ""
                  ? "blue"
                  : data === ""
                  ? "geekblue"
                  : "green"
              }
            >
              {data}
            </Tag>
          );
        }
      },
      // {
      //   title: 'Tags',
      //   key: 'tags',
      //   dataIndex: 'tags',
      //   render: tags => (
      //     <span>
      //       {tags.map(tag => {
      //         let color = tag.length > 5 ? 'geekblue' : 'green';
      //         if (tag === 'loser') {
      //           color = 'volcano';
      //         }
      //         return (
      //           <Tag color={color} key={tag}>
      //             {tag.toUpperCase()}
      //           </Tag>
      //         );
      //       })}
      //     </span>
      //   ),
      // },
      {
        title: "",
        key: "action",
        width: 150,
        render: (text, record) => (
          <span>
            <a
              onClick={() => {
                this.onCheck(record);
              }}
            >
              /
            </a>
          </span>
        )
      }
    ];
    return (
      <div className="approveList">
        {page === "listPage" ? (
          <Spin spinning={spin}>
            <Table
              key="1"
              className="approveList-table"
              columns={columns}
              dataSource={data}
              scroll={{ x: 1000, y: "calc(100vh - 220px)" }}
            />
          </Spin>
        ) : (
          checkPage
        )}
      </div>
    );
  }
}
export default ApproveList;
