import React, { Component } from "react";
import {
  Modal,
  Tag,
  Button,
  Table,
  message,
  Spin,
  Form,
  Input,
  Divider
} from "antd";
import "./ShowProject.less";
import http from "../../../utils/api";
import CommitCard from "../ApplyProject/CommitCard";
const showProjectId = "622207867138";
const applyProjectId = "620475440053";
const suggestId = "621432069832";
const { TextArea } = Input;

class ShowProject extends React.Component {
  state = {
    data: [{}],
    page: "listPage",
    spin: false,
    record: {},
    visible: false
  };
  componentDidMount = async () => {
    await this.getData();
  };
  componentWillUnmount() {
    // 
    this.setState = (state, callback) => {
      return;
    };
  }
  onCheck = record => {
    this.setState({
      page: "checkPage",
      record
    });
  };
  onBack = () => {
    this.setState({
      page: "listPage"
    });
  };
  handleCancel = () => {
    this.setState({
      visible: false
    });
  };
  onSuggest = () => {
    this.setState({
      visible: true
    });
  };
  onjoin = async record => {
    const doctorInfo = JSON.parse(localStorage.getItem("doctorInfo"));
    record.joinPersonID = doctorInfo.doctorId;
    record.number = "";
    let res;
    try {
      res = await http().addRecords({
        resid: applyProjectId,
        data: [record]
      });
      if (res.data.Error == 0) {
        message.success("");
      }
      await this.getData();
      this.props.onUpdate();
    } catch (error) {
      message.error(error.message);
    }
  };
  submitSuggest = async e => {
    const { record } = this.state;
    this.props.form.validateFieldsAndScroll(async (err, values) => {
      if (err) {
        return;
      }
      let res;
      let data = [
        {
          suggest: values.suggest,
          projectId: record.REC_ID
        }
      ];
      try {
        res = await http().addRecords({
          resid: suggestId,
          data
        });
        if (res.data.Error === 0) {
          message.success("");
        }
      } catch (error) {
        message.error(error.message);
      }
      this.setState({
        visible: false
      });
    });
  };
  getApplyData = async () => {
    let res;
    try {
      res = await http().getTable({
        resid: applyProjectId
      });
      if (res.data.error === 0) {
        let data = [];
        // 
        res.data.data.map(item => {
          if (item.joinPersonID) {
            data.push(item.applyId);
          }
        });
        return data;
      }
    } catch (error) {
      message.error(error.message);
    }
  };
  getData = async () => {
    const doctorInfo = JSON.parse(localStorage.getItem("doctorInfo"));
    this.setState({
      spin: true
    });
    let res;
    try {
      res = await http().getTable({
        resid: showProjectId
      });
      if (res.data.error === 0) {
        let applyData = await this.getApplyData();
        let data = [];
        res.data.data.map((item,index) => {
          //   isJoinedtrue
          if (
            applyData.includes(item.applyId) ||
            item.doctorId == doctorInfo.doctorId
          ) {
            item.isJoined = true;
            item.number = index+1;
            data.push(item);
          } else {
            item.number = index+1;
            data.push(item);
          }
        });
        this.setState({
          data
        });
      }
    } catch (error) {}

    this.setState({
      spin: false
    });
  };

  render() {
    const { data, spin, page, record } = this.state;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    };
    const columns = [
      {
        title: "",
        dataIndex: "number",
        key: "number",
        width: 80
      },
      {
        title: "",
        dataIndex: "applyTime",
        key: "applyTime",
        width: 150
      },
      {
        title: "",
        dataIndex: "task",
        key: "hospital",
        width: 150
      },
      {
        title: "",
        dataIndex: "doctor",
        key: "doctor",
        width: 150
      },
      {
        title: "",
        dataIndex: "status",
        key: "status",
        width: 150,
        render: data => {
          return (
            <Tag
              color={
                data === ""
                  ? "blue"
                  : data === ""
                  ? "geekblue"
                  : "green"
              }
            >
              {data}
            </Tag>
          );
        }
      },
      {
        title: "",
        dataIndex: "action",
        key: "action",
        width: 150,
        render: (text, record) => {
          return (
            <span>
              <a
                onClick={() => {
                  this.onCheck(record);
                }}
              >
                
              </a>
              <Divider type="vertical" />
              {!record.isJoined ? (
                <a
                  onClick={() => {
                    this.onjoin(record);
                  }}
                >
                  
                </a>
              ) : null}
            </span>
          );
        }
      }
    ];
    const checkPage = (
      <React.Fragment>
        {page === "checkPage" ? (
          <Button
            icon={"rollback"}
            type="primary"
            onClick={this.onBack}
            className="approveList-btn"
          >
            
          </Button>
        ) : null}
        <Form
          {...formItemLayout}
          className="showProject-form"
          id="applyProjectForm"
        >
          <div className="applyProject-form-contain">
            <div className="applyProject-form-contain-info">
              <h1 style={{ textAlign: "center" }}>CHASE-IBD</h1>
              <h3 style={{ textAlign: "center" }}></h3>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.task}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.taskPrincipal}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.doctor}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.hospital}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.post}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.phoneNumber}</span>
              </Form.Item>
              <Form.Item label={<span>E-mail&nbsp;</span>}>
                <span>{record.email}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.inlandUnit}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.foreignUnit}</span>
              </Form.Item>
              {/* <Form.Item label={<span>IBD&nbsp;</span>}>
          <span>{record.teamPeople}</span>&nbsp;&nbsp;
          <span>{record.teamPeople2}</span>&nbsp;&nbsp;
          <span>{record.teamPeople3}</span>&nbsp;&nbsp;
        </Form.Item> */}
              {/* <Form.Item label={<span>&nbsp;</span>}>
          <Checkbox.Group
            options={options}
            defaultValue={record.studyType}
            onChange={this.onChangeCheckbox}
          />
        </Form.Item> */}
              <h3 style={{ textAlign: "center" }}></h3>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.studyReason}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.studyArea}</span>
              </Form.Item>
              <Form.Item
                label={
                  <span>&nbsp;</span>
                }
              >
                <span>{record.studyTarget}</span>
              </Form.Item>
              <Form.Item
                label={
                  <span>&nbsp;</span>
                }
              >
                <span>{record.studyLast}</span>
              </Form.Item>
              <Form.Item
                label={
                  <span>
                    &nbsp;
                  </span>
                }
              >
                <span>{record.studyDescription}</span>
              </Form.Item>
              <Form.Item
                label={
                  <span>
                    &nbsp;
                  </span>
                }
              >
                <span>{record.studySelected}</span>
              </Form.Item>
              <Form.Item
                label={
                  <span>&nbsp;</span>
                }
              >
                <span>{record.studyLast}</span>
              </Form.Item>
              <Form.Item
                label={<span>&nbsp;</span>}
              >
                <span>{record.studyAssess}</span>
              </Form.Item>
              <Form.Item
                label={<span>&nbsp;</span>}
              >
                <span>{record.studyCheck}</span>
              </Form.Item>
              <Form.Item
                label={<span>&nbsp;</span>}
              >
                <span>{record.studyAssess}</span>
              </Form.Item>
              <Form.Item
                label={
                  <span>
                    &nbsp;
                  </span>
                }
              >
                <span>{record.staticMethod}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.standard}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.sure}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.committee}</span>
              </Form.Item>
              <Form.Item label={<span>&nbsp;</span>}>
                <span>{record.referenceData}</span>
              </Form.Item>
            </div>
            {record["621432069832"] ? (
              <div className="applyProject-form-contain-notice">
                <span></span>
                {record["621432069832"] &&
                  record["621432069832"].map(item => {
                    return (
                      <CommitCard key={item.REC_ID} data={item}></CommitCard>
                    );
                  })}
              </div>
            ) : null}
          </div>
        </Form>
      </React.Fragment>
    );
    return (
      <div className="approveProjectList">
        {page === "listPage" ? (
          <Spin spinning={spin}>
            <Table
              key="2"
              className="approveProjectList-table"
              columns={columns}
              dataSource={data}
              scroll={{ x: 1000, y: "calc(100vh - 220px)" }}
            />
          </Spin>
        ) : (
          checkPage
        )}
      </div>
    );
  }
}
export default Form.create()(ShowProject);
