import React, { Component } from "react";
import "./Home.less";
import {
  Menu,
  Icon,
  Button,
  Tabs,
  Upload,
  Popover,
  Alert,
  Form,
  Modal,
  Input,
  message,
  Spin
} from "antd";
import logo from "../../../assets/images/logo.png";
import NoticeList from "../NoticeList";
import ApplyDataBase from "../ApplyDataBase";
import ApplyProject from "../ApplyProject";
import ActApply from "../ActApply";
import MenuDataBase from "../MenuDataBase/MenuDataBase";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import MenuCommittee from "../MenuCommittee";
import http from "../../../utils/api";
const { TabPane } = Tabs;
const doctorInfoId = "622204211916";
const managerInfoId = '623858273868'
class Home extends Component {
  state = {
    collapsed: false,
    imageUrl: "",
    userInfo: {},
    currentItem: 1,
    visible: false,
    doctorInfo: {},
    isManager:false, //
    managerInfo:[],
    loading:false
  };
  constructor(props) {
    super(props);
  }
  toggleCollapsed = () => {
    this.setState({
      collapsed: !this.state.collapsed
    });
  };
  //
  getManagerInfo = async(userInfo) => {
    let res;
    try {
      res = await http().getTable({
        resid: managerInfoId,
      });
      if (res.data.error === 0) {
      res.data.data.map((item) => {
       if( item.phone === userInfo.UserInfo.EMP_HANDPHONE){
         localStorage.setItem("isManager",JSON.stringify(true))
         this.setState({
           isManager:true
         })
       }
      })
        this.setState({
          managerInfo: res.data.data[0]
        });
      }
    } catch (error) {
      message.error(error.message);
    }
  }
  getDoctorInfo = async(userInfo) => {
    let res;
    try {
      res = await http().getTable({
        resid: doctorInfoId,
        cmswhere: `phoneNumber = ${userInfo.UserInfo.EMP_HANDPHONE}`
      });
      if (res.data.error === 0) {
        localStorage.setItem("doctorInfo",JSON.stringify(res.data.data[0]))
        this.setState({
          doctorInfo: res.data.data[0]
        });
      }
    } catch (error) {
      message.error(error.message);
    }
  }
  componentDidMount = async () => {
    this.setState({
      loading:true
    })
    const userInfo = JSON.parse(localStorage.getItem("userInfo"));
    const currentItem = JSON.parse(localStorage.getItem("currentItem"));
    const isManager = JSON.parse(localStorage.getItem("isManager"));
    if (currentItem) {
      this.setState({
        userInfo,
        currentItem
      });
    } else {
      this.setState({
        userInfo
      });
    }
    if(!isManager){
      await this.getManagerInfo(userInfo);
    }else{
      this.setState({
        isManager:true
      })
    }
    await this.getDoctorInfo(userInfo);
     this.setState({
       loading:false
     })
  };
  getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.addEventListener("load", () => callback(reader.result));
    reader.readAsDataURL(img);
  };
  //
  onEditInfo = async () => {
    this.setState({
      visible: true
    });
  };
  onSave = () => {
    const { form } = this.props;
    let res;
    this.props.form.validateFieldsAndScroll(async (err, values) => {
      if (err) {
        return;
      }
      let data = [
        {
          occupationNumber: form.getFieldValue("postNumber"), //
          hospital: form.getFieldValue("hospital"), // 
          doctor: form.getFieldValue("doctor"),
          post: form.getFieldValue("post"),
          emial: form.getFieldValue("emial"),
          REC_ID: this.state.doctorInfo.REC_ID
        }
      ];
      try {
        res = await http().modifyRecords({
          resid: doctorInfoId,
          data
        });
        if (res.data.Error === 0) {
          message.success("");
          this.setState({
          doctorInfo: res.data.data[0]
          })
        }
      } catch (error) {
        message.error(error.message);
      }
      this.setState({
        visible: false
      });
    });
  };
  handleCancel = () => {
    this.setState({
      visible: false
    });
  };
  //
  logout = () => {
    localStorage.removeItem("userInfo");
    localStorage.removeItem("currentItem");
    this.props.history.replace({
      pathname: "/"
    });
  };
  //
  onMenuDetail = name => {
    if (name === "menuCommittee") {
      localStorage.setItem("currentItem", 2);
      this.setState({
        currentItem: 2
      });
    } else {
      localStorage.setItem("currentItem", 1);
      this.setState({
        currentItem: 1
      });
    }
    this.props.history.push({
      pathname: `/${name}`
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { doctorInfo } = this.state;
    const uploadButton = (
      <div>
        <div className="ant-upload-text"></div>
      </div>
    );
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const content = (
      <React.Fragment>
        <div
          style={{
            margin: "10px 0 ",
            display: "flex",
            flexDirection: "column"
          }}
        >
          <Button
            type="primary"
            icon="edit"
            size="small"
            onClick={this.onEditInfo}
          >
            
          </Button>
          <Button
            type="primary"
            icon="logout"
            size="small"
            onClick={this.logout}
            style={{ marginTop: "10px" }}
          >
            
          </Button>
        </div>
      </React.Fragment>
    );
    const { imageUrl, userInfo, currentItem,isManager ,loading} = this.state;
    return (
      <Spin spinning={loading}>
      <div className="home">
        <Modal
          title=""
          visible={this.state.visible}
          width={600}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          destroyOnClose={true}
          footer={[
            <Button key="back" onClick={this.handleCancel}>
              
            </Button>,
            <Button
              key="submit"
              type="primary"
              onClick={() => {
                this.onSave();
              }}
            >
              
            </Button>
          ]}
        >
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="home-modal">
            <Form.Item label="">
              {getFieldDecorator("doctor", {
                rules: [{ required: true, message: "!" }],
                initialValue: doctorInfo.doctor
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder=""
                />
              )}
            </Form.Item>
            <Form.Item label="">
              {getFieldDecorator("hospital", {
                rules: [{ required: true, message: "!" }],
                initialValue: doctorInfo.hospital
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder=""
                />
              )}
            </Form.Item>
            <Form.Item label=''>
              {getFieldDecorator("post", {
                rules: [{ required: true, message: "!" }],
                initialValue: doctorInfo.post
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder=""
                />
              )}
            </Form.Item>
            <Form.Item label=''>
              {getFieldDecorator("postNumber", {
                rules: [{ required: true, message: "!" }],
                initialValue: doctorInfo.occupationNumber
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder=""
                />
              )}
            </Form.Item>
            <Form.Item label=''>
              {getFieldDecorator("email", {
                rules: [{ required: true, message: "!" }],
                initialValue: doctorInfo.email
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder=""
                />
              )}
            </Form.Item>
          </Form>
        </Modal>
        <div className="home-left">
          <Menu
            selectedKeys={[`${currentItem}`]}
            defaultOpenKeys={["sub1"]}
            mode="inline"
            theme="dark"
            style={{ height: "100vh" }}
            inlineCollapsed={this.state.collapsed}
          >
            <div className="home-left-logo">
              <img className="home-left-logo-img" src={logo}></img>
            </div>
            <Menu.Item
              key="1"
              onClick={() => {
                this.onMenuDetail("home");
              }}
            >
              <Icon type="pie-chart" />
              <span>CHASE-IBD</span>
            </Menu.Item>
            {isManager? <Menu.Item
              key="2"
              onClick={() => {
                this.onMenuDetail("menuCommittee");
              }}
            >
              <Icon type="desktop" />
              <span></span>
            </Menu.Item>:null}
           
          </Menu>
        </div>
        <div className="home-right">
          <div className="home-right-top">
            <div className="home-right-top-tabs">
              <Route path="/home" component={MenuDataBase}></Route>
              <Route path="/menuCommittee" component={MenuCommittee}></Route>
            </div>

            <div className="home-right-top-user">
              <Popover content={content} placement="bottom">
                <div>
                  <Icon type="user" style={{ fontSize: "20px" }} />
                </div>
                <div>{doctorInfo.doctor}</div>
              </Popover>
            </div>
          </div>
          <div className="home-right-content">
            <div></div>
          </div>
        </div>
      </div></Spin>
    );
  }
}

export default Form.create()(Home);
